.top-right {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 99;
}
.relative {
  position: relative;
}
.blue-transparent {
  background-color: rgba(130, 177, 255, 0.8) !important;
  font-size: 125% !important;
  font-weight: 400 !important;
}
p span {
  background-color: #e4e4e4;
  padding: 5px 10px 5px 10px;
  margin-left: 5px;
  border-radius: 10px;
}

.sticky {
  position: sticky;
  top: 0;
}
.brand {
  margin-left: 0.75rem;
  font-weight: bold;
}
